export function useFormatToDrugLine(line) {
    let lineStr = "";
    for (let index = 1; index < 8; index++) {
        lineStr += line[`dose${index}`] ? line[`dose${index}`] + "~" : "~";
        lineStr += line[`time${index}`] ? line[`time${index}`] + "~" : "~";
        lineStr += line[`initial${index}`] ? line[`initial${index}`] : "";
        if (index != 7) lineStr += "|";
    }
    return lineStr;
}

export function useBpO2PulseLineFormatter(line) {
    let lineStr = "";
    for (let index = 1; index < 18; index++) {
        lineStr += line[`timeInit${index}`] + (index != 17 ? "|" : "");
    }
    return lineStr;
}

export function useTineLineFormatter(line) {
    let lineStr = "";
    for (let index = 1; index < 18; index++) {
        lineStr += line[`timeInit${index}`].part1 + "~";
        lineStr += line[`timeInit${index}`].part2 + (index != 17 ? "|" : "");
    }
    return lineStr;
}
