<script>
import { inject, onMounted, onUnmounted } from "vue";
import { useDrugChartForm, useFormatToDrugLine } from "@/composable/opu";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const drugChartForm = useDrugChartForm();

        const onSummaryLoaded = () => {
            drugChartForm.value = store.getters["opu/getDrugChart"];
        };

        const opuForm = inject("opuForm");
        const formatToLine = (data) => {
            if (data.key === "ativanLine") {
                opuForm.ativanLine = useFormatToDrugLine(data);
            } else if (data.key === "versedLine") {
                opuForm.versedLine = useFormatToDrugLine(data);
            } else if (data.key === "fentanylLine") {
                opuForm.fentanylLine = useFormatToDrugLine(data);
            } else if (data.key === "narcanLine") {
                opuForm.narcanLine = useFormatToDrugLine(data);
            } else if (data.key === "atropineLine") {
                opuForm.atropineLine = useFormatToDrugLine(data);
            } else if (data.key === "gravolLine") {
                opuForm.gravolLine = useFormatToDrugLine(data);
            } else if (data.key === "xylocaineLine") {
                opuForm.xylocaineLine = useFormatToDrugLine(data);
            } else if (data.key === "other1Line") {
                opuForm.other1Name = data.name;
                opuForm.other1Line = useFormatToDrugLine(data);
            } else if (data.key === "other2Line") {
                opuForm.other2Name = data.name;
                opuForm.other2Line = useFormatToDrugLine(data);
            } else if (data.key === "other3Line") {
                opuForm.other3Name = data.name;
                opuForm.other3Line = useFormatToDrugLine(data);
            } else if (data.key === "other4Line") {
                opuForm.other4Name = data.name;
                opuForm.other4Line = useFormatToDrugLine(data);
            } else if (data.key === "other5Line") {
                opuForm.other5Name = data.name;
                opuForm.other5Line = useFormatToDrugLine(data);
            } else if (data.key === "other6Line") {
                opuForm.other6Name = data.name;
                opuForm.other6Line = useFormatToDrugLine(data);
            }
        };

        onMounted(() => {
            emitter.on("opu:summary-loaded", onSummaryLoaded);
        });

        onUnmounted(() => {
            emitter.off("opu:summary-loaded", onSummaryLoaded);
        });

        return { drugChartForm, formatToLine };
    }
};
</script>
