<template>
    <main id="opu" class="p-inputtext-sm p-input-filled">
        <p-card>
            <template #content>
                <div class="p-fluid p-grid">
                    <div class="p-col-fixed">
                        <div class="p-field p-grid">
                            <label for="date" class="p-col-fixed p-mb-0">
                                Date
                            </label>
                            <div class="p-col">
                                <p-calendar id="date" />
                            </div>
                        </div>
                    </div>

                    <div class="p-col-fixed">
                        <div class="p-field p-grid">
                            <label for="md" class="p-col-fixed p-mb-0">
                                MD
                            </label>
                            <div class="p-col">
                                <p-dropdown
                                    id="md"
                                    :options="options"
                                    optionLabel="label"
                                />
                            </div>
                        </div>
                    </div>

                    <div class="p-col-fixed">
                        <div class="p-field p-grid">
                            <label for="iv-started" class="p-col-fixed p-mb-0">
                                IV Started
                            </label>
                            <div class="p-col">
                                <p-input-number id="iv-started" showButtons />
                            </div>
                        </div>
                    </div>

                    <div class="p-col-fixed">
                        <div class="p-field p-grid">
                            <label for="by" class="p-col-fixed p-mb-0">
                                By
                            </label>
                            <div class="p-col">
                                <p-dropdown
                                    id="by"
                                    :options="options"
                                    optionLabel="label"
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </template>
        </p-card>
        <opu-drug-chart />

        <opu-middle-form />

        <opu-patient-chart type="pre" />

        <opu-bottom-form />

        <opu-patient-chart type="post" />

        <p-toolbar class="p-p-2 p-shadow-2">
            <template #left>
                <p-button label="Save" class="p-mr-2" @click="save" />
                <p-button label="PDF Report" class="p-mr-2" />
                <p-button label="Delete" class="p-mr-2" />
            </template>
        </p-toolbar>
    </main>
</template>

<script>
import { onMounted, onUnmounted, provide, reactive, ref } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";

import { useToast } from "primevue/usetoast";

import OpuDrugChart from "@/components/opu/drug-chart.vue";
import OpuPatientChart from "@/components/opu/patient-chart.vue";
import OpuMiddleForm from "@/components/opu/middle-form.vue";
import OpuBottomForm from "@/components/opu/bottom-form.vue";
import {
    useBpO2PulseLineFormatter,
    useFormatToDrugLine,
    useOpuForm,
    useTineLineFormatter
} from "@/composable/opu";

export default {
    components: {
        OpuDrugChart,
        OpuPatientChart,
        OpuMiddleForm,
        OpuBottomForm
    },
    setup() {
        const store = useStore();
        const toast = useToast();

        const opuForm = useOpuForm();
        provide("opuForm", opuForm);

        const options = ref([
            { label: "Opt 1", value: "opt-1" },
            { label: "Opt 2", value: "opt-2" },
            { label: "Opt 3", value: "opt-3" }
        ]);

        const save = () => {
            emitter.emit("app:show-page-loading", true);
            store.dispatch("opu/saveSummary", { ...opuForm }).then((data) => {
                getOpuSummary();
            });
        };

        const onSummaryLoaded = () => {
            opuForm.opuId = store.state.opu.opuId;
            opuForm.cycleId = store.state.opu.cycleId;
            opuForm.patientId = store.state.opu.patientId;
            opuForm.docDate = store.state.opu.docDate;
            opuForm.ativanLine = useFormatToDrugLine(
                store.state.opu.ativanLine
            );
            opuForm.versedLine = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.fentanylLine = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.narcanLine = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.atropineLine = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.gravolLine = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.xylocaineLine = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.other1Line = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.other2Line = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.other3Line = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.other4Line = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.other5Line = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.other6Line = useFormatToDrugLine(
                store.state.opu.versedLine
            );
            opuForm.other1Name = store.state.opu.other1Name;
            opuForm.other2Name = store.state.opu.other2Name;
            opuForm.other3Name = store.state.opu.other3Name;
            opuForm.other4Name = store.state.opu.other4Name;
            opuForm.other5Name = store.state.opu.other5Name;
            opuForm.other6Name = store.state.opu.other6Name;

            opuForm.preTimeLine = useTineLineFormatter(
                store.state.opu.preTimeLine
            );
            opuForm.preBPLine = useBpO2PulseLineFormatter(
                store.state.opu.preBPLine
            );
            opuForm.preO2Line = useBpO2PulseLineFormatter(
                store.state.opu.preO2Line
            );
            opuForm.prePulseLine = useBpO2PulseLineFormatter(
                store.state.opu.prePulseLine
            );

            opuForm.postTimeLine = useTineLineFormatter(
                store.state.opu.postTimeLine
            );
            opuForm.postBPLine = useBpO2PulseLineFormatter(
                store.state.opu.preBPLine
            );
            opuForm.postO2Line = useBpO2PulseLineFormatter(
                store.state.opu.postO2Line
            );
            opuForm.postPulseLine = useBpO2PulseLineFormatter(
                store.state.opu.postPulseLine
            );
        };

        const getOpuSummary = () => {
            emitter.emit("app:show-page-loading", true);
            store
                .dispatch(
                    "opu/getOPUSummary",
                    store.state.artCycle.selectedCycle.cycleId
                    // 2000003480 // todo remmove
                )
                .then(() => {
                    emitter.emit("opu:summary-loaded");
                    emitter.emit("app:show-page-loading", false);
                })
                .catch((error) => {
                    emitter.emit("app:show-page-loading", false);
                });
        };

        onMounted(() => {
            emitter.on("opu:summary-loaded", onSummaryLoaded);

            if (!store.state.artCycle.selectedCycle) {
                toast.add({
                    severity: "warn",
                    summary: "Warnning!",
                    detail: "Please select cycle first",
                    life: 3000
                });
                return;
            }

            getOpuSummary();
        });

        onUnmounted(() => {
            emitter.off("opu:summary-loaded", onSummaryLoaded);
        });

        return { options, save };
    }
};
</script>

<style>
#opu .p-card .p-card-content {
    padding: 0 0;
}

#opu .p-field {
    margin-bottom: 0.5rem;
}

#opu .p-card {
    height: 100%;
}

#opu table .p-inputtext {
    width: -webkit-fill-available;
}

#opu .p-inputnumber {
    height: 24.5px;
}
</style>
